.pop {
  width: 100%; height: 100vh;
  background: rgba(0,0,0,0.9);
  padding: 5vw;
  position: fixed;
  top: 0; left: 0;
  z-index: 10;
  .con {
    width: 100%; height: 100%;
    iframe {
      width: 100%; height: 100%;
      object-fit: cover;
    }
    img {
      width: 100%; height: 100%;
      object-fit: contain;
    }
  }
  .close {
    font: bold 14px/1 'arial';
    color: orange;
    position: absolute;
    top: 40px; right: 5vw;
    cursor: pointer;
  }
}